
import Loading from 'vux/src/components/loading/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import Step from 'vux/src/components/step/step.vue'
import StepItem from 'vux/src/components/step/step-item.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Picker from 'vux/src/components/picker/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Box from 'vux/src/components/box/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import base64 from 'vux/src/tools/base64/index.js'

import AppHeader from './Header'
import AppFooter from './Footer'

import axios from 'axios'
import qs from 'qs'
import stringify from 'fast-json-stable-stringify'

import {initApi,showApi,submitApi} from '../utils/apis'
import {getUserToken, setUserToken, getPath} from '../utils/common'

export default {
directives: {
TransferDom
},
components: {
AppHeader,
AppFooter,
Loading,
Toast,
Step,
StepItem,
PopupPicker,
Group,
Picker,
XButton,
Box,
Confirm
},
data(){
return {
loadingShow:false,
loadingText:'...',
toastText:'',
toastShow:false,
titleDetail:'',
step:1,
title1:'',
title2:'',
title3:'',
value1: [],
value2: [],
value3: [],
list1:[],
list2:[],
list3:[],
tips2:'',
tips3:'',
confirmTitle:'',
loginDisabled:false,
show:false,
}

},
mounted:function(){
let _this = this;
let userToken = getUserToken();
if(userToken){

let formdata = {"type":"building"};
let postval = "key="+base64.encode(encodeURI(stringify(formdata)));



axios.get(initApi+'?token='+userToken)
.then(function(res){
if(res.data.code === 1){
if(res.data.data.steps !== 'select'){
let nowPath = getPath(res.data.data.steps);
_this.$router.push({path:nowPath});
}
}
})
.catch(function(err){
console.log(err);
});

axios.post(showApi + '?token='+userToken,postval)
.then(function(res){
if(res.data.code === 0){
}
if(res.data.code === 1){
_this.list1 = [res.data.data.data];
_this.titleDetail = "<span style=\"color:red;\">" + res.data.data.dormitory_number + "</span><span style=\"color:red;\">" + res.data.data.bed_number +"</span>";
}
})
.catch(function(err){
console.log(err);
});
}else{
this.$router.push({path: '/login'});
}
},
methods: {
change1(val){
let _this = this;
_this.loadingShow = true;
let formdata = {"type":"dormitory","building":val[0]};

let postval = "key="+base64.encode(encodeURI(stringify(formdata)));
axios.post(showApi + '?token='+getUserToken(),postval)
.then(function(res){
if(res.data.code === 0){
_this.loadingShow = false;
_this.loadingText = res.data.msg;
_this.loadingShow = true;
setTimeout(function (){
_this.loadingShow = false;
},2000);
}
if(res.data.code === 1){
_this.loadingShow = false;
_this.tips2 = "";
_this.list2 = [res.data.data.data];
}
})
.catch(function(err){
console.log(err);
});
},
change2(val){
let _this = this;
_this.loadingShow = true;
let formdata = {"type":"bed","building":_this.value1[0],"dormitory":val[0]};
let postval = "key="+base64.encode(encodeURI(stringify(formdata)));
axios.post(showApi + '?token='+getUserToken(),postval)
.then(function(res){
if(res.data.code === 0){
_this.loadingShow = false;
_this.loadingText = res.data.msg;
_this.loadingShow = true;
setTimeout(function (){
_this.loadingShow = false;
},2000);
}
if(res.data.code === 1){
_this.loadingShow = false;
_this.tips3 = "";
_this.list3 = [res.data.data.data];
}
})
.catch(function(err){
console.log(err);
});
},
confirmButton(){
this.confirmTitle = ""+this.value1[0]+'#'+this.value2[0]+'-'+this.value3[0];
this.show = true
},
onConfirm(){
let _this = this;
_this.loadingText = "...";
_this.loadingShow = true;
let formdata = {
"dormitory_id": _this.value1[0]+"#"+_this.value2[0],
"bed_id":_this.value3[0]
};
let postval = "key="+base64.encode(encodeURI(stringify(formdata)));
axios.post(submitApi + '?token='+getUserToken(),postval)
.then(function(res){
if(res.data.code === 0){
}
if(res.data.code === 1){
_this.loadingShow = false;
_this.toastText = res.data.msg
_this.toastShow = true

setTimeout(function (){
_this.$router.push({path: '/confirm'});
}, 2000);

}
})
.catch(function(err){
console.log(err);
});
}

}
}



