
import XInput from 'vux/src/components/x-input/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Box from 'vux/src/components/box/index.vue'
import XAddress from 'vux/src/components/x-address/index.vue'
import ChinaAddressV4Data from 'vux/src/datas/china_address_v4.json'
import Divider from 'vux/src/components/divider/index.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import XNumber from 'vux/src/components/x-number/index.vue'
import Selector from 'vux/src/components/selector/index.vue'
import Checklist from 'vux/src/components/checklist/index.vue'
import Step from 'vux/src/components/step/step.vue'
import StepItem from 'vux/src/components/step/step-item.vue'
import Toast from 'vux/src/components/toast/index.vue'
import value2name from 'vux/src/filters/value2name.js'
import base64 from 'vux/src/tools/base64/index.js'

import Uploader from 'vux-uploader'
import AppHeader from './Header'
import AppFooter from './Footer'

import axios from 'axios'
import qs from 'qs'
import stringify from 'fast-json-stable-stringify'

import {initApi,setinfoApi,qiniuTokenApi,imageCDN } from '../utils/apis'
import {getUserToken, setUserToken, getPath} from '../utils/common'

export default {
directives: {
TransferDom
},
components: {
AppHeader,
AppFooter,
XInput,
Popup,
XButton,
Group,
Cell,
Box,
XAddress,
Uploader,
Divider,
Checker,
CheckerItem,
XNumber,
Selector,
Checklist,
Step,
StepItem,
Toast
},
data () {
return {
step: 0,
XM:'',
XB:'',
YXMC:'',      RXQHK: '',//
mobile:'',
JTRKS: 1,//3
JTZYCY: '',
YZBM: '716000',
BRDH:'13812345678',//
SZDQ:["610000", "610100", "610112"],//
SZDQ_CN: '  ',//
XXDZ:'',//
JTSR:0,
GXoption:['','','','','','',''],
infoArr: [],

addressData: ChinaAddressV4Data,
addressArea: '',
images:[
{url:'//www.chd.edu.cn/_upload/tpl/00/33/51/template51/images/logo.png'}
],//
uploadUrl:'//upload-z2.qiniup.com',
uploadToken:'',
loginDisabled: false,
jjdc:[[1],[1],[1],[1],[1],[1],[1],[1,2,3,4]],//
JTJJtitle1:'1.',
JTJJoption1:[
{key:'1',value:''},
{key:'2',value:''},
{key:'3',value:''},
{key:'4',value:''},
{key:'5',value:''},
],
JTJJtitle2:'2.',
JTJJoption2:[
{key:'1',value:''},
{key:'2',value:''},
{key:'3',value:''},
{key:'4',value:''},
{key:'5',value:''},
],
JTJJtitle3:'3.',
JTJJoption3:[
{key:'1',value:''},
{key:'2',value:''},
{key:'3',value:'3-4'},
{key:'4',value:'1-2'},
{key:'5',value:''},
],
JTJJtitle4:'4.',
JTJJoption4:[
{key:'1',value:''},
{key:'2',value:''},
{key:'3',value:'3-4'},
{key:'4',value:'1-2'},
{key:'5',value:''},
],
JTJJtitle5:'5.',
JTJJoption5:[
{key:'1',value:'2000'},
{key:'2',value:'2000-11'},
{key:'3',value:'1-33'},
{key:'4',value:'3'},
],
JTJJtitle6:'6.',
JTJJoption6:[
{key:'1',value:''},
{key:'2',value:''},
{key:'3',value:''},
{key:'4',value:''},
],
JTJJtitle7:'7.',
JTJJoption7:[
{key:'1',value:''},
{key:'2',value:''},
{key:'3',value:''},
],
JTJJtitle8:'8.',
JTJJoption8:[
{key:'1',value:''},
{key:'2',value:''},
{key:'3',value:''},
{key:'4',value:''},
],
showInfo: false,
toastText:'',
toastShow:false,
toastText2:'',
toastShow2:false,
confirmDisabled:false,
confirmShowLoading:false,
}
},
mounted:function(){
let _this = this;
let userToken = getUserToken();



if(userToken){

axios.get(initApi + '?token='+userToken)
.then(function(res){
if(res.data.code === 0){
}
if(res.data.code === 1){
if(res.data.data.steps !== 'setinfo'){
let nowPath = getPath(res.data.data.steps);
_this.$router.push({path:nowPath});
}
_this.XM = res.data.data.info.XM;
_this.XB = res.data.data.info.XB;
_this.YXMC = res.data.data.info.YXMC;
}
})
.catch(function(err){
console.log(err);
});


axios.get(qiniuTokenApi + '?token=' + userToken)
.then(function(res){
if(res.data.code === 0){
}
if(res.data.code === 1){
_this.uploadToken = res.data.data;
}
})
.catch(function(err){
console.log(err);
});


for(let i=0;i<this.JTRKS - 1;i++){
let obj = {index:i,name:'',age:'',relation:'',unit:'',job:'',income:'',health:'',mobile:''};
this.infoArr.push(obj);
}
}else{
_this.$router.push({path: '/'});
}
},
methods:{
change(val){

this.infoArr = [];


for(let i=0;i<this.JTRKS - 1;i++){
let obj = {index:i,name:'',age:'',relation:'',unit:'',job:'',income:'',health:'',mobile:''};
this.infoArr.push(obj);
}
},
submitButton(){

this.SZDQ_CN = value2name(this.SZDQ, ChinaAddressV4Data);
this.showInfo = true;
},
confirmButton(){
let _this = this;

_this.confirmDisabled = true;
_this.confirmShowLoading = true;

let formdata = {
'BRDH': _this.BRDH,//
'RXQHK':_this.RXQHK,//
'JTRKS':_this.JTRKS,//
'YZBM':_this.YZBM,//
'SZDQ':_this.SZDQ_CN ,//
'XXDZ':_this.XXDZ,//
'ZSR':_this.JTSR,//
'JTRK':_this.infoArr,//
'JJDC':_this.jjdc,//
'ZP':_this.images,//
};

let postval = "key="+base64.encode(encodeURI(stringify(formdata)));


axios.post(setinfoApi + '?token='+getUserToken(), postval)
.then(function(res){

if(res.data.code === 0){
_this.toastText2 = res.data.msg;
_this.toastShow2 = true;
}
if(res.data.code === 1){

_this.$router.push({path: '/selectdormitory'});
}
})
.catch(function(err){
console.log(err);
});
},

changeArea(val){
},

previewMethod(){},
addImageMethod(){
},
removeImageMethod(){},
uploadImage(){
let _this = this;
let file = _this.$refs.upload;


let config = {
headers:{'Content-Type':'multipart/form-data'}
};
let formdata = new FormData();
formdata.append("token",_this.uploadToken);
formdata.append("file",file.$refs.input.files[0]);

axios.post(_this.uploadUrl,formdata,config)
.then(function(res){
if(res.data.key){
let imgObj = {url:imageCDN + res.data.key};
_this.images.push(imgObj);
}
})
.catch(function(err){
console.log(err);
})
},
}
}



